nav {
  position: fixed;
  width: 100%;
  display: flex;
  flex-direction: column;
  background: #222;
  z-index: 300;
}

.menu-wrap {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  background: #222;
  z-index: 200;
}

#monstera {
  height: 1.5rem;
}

nav h3 {
  font-size: 1.5rem;
  color: white;
  font-family: 'Roboto Mono', monospace;
  margin: 0;
}

nav ul {
  visibility: hidden;
  text-align: center;
  width: 100%;
  transform: translateY(-12rem);
  transition: transform .3s .3s;
}

nav ul li {
  font-size: 1.5rem;
  text-transform: lowercase;
  font-family: 'Roboto Mono', monospace;
  height: 0rem;
  opacity: 0;
  transition: height .3s .3s, opacity .1s .4s;
  z-index: 100;
}

nav ul li a {
  height: 0rem;
  transition: all .3 .3s;
}

nav a,
nav a:hover,
nav a:focus,
nav a:active,
nav a:visited {
  font-weight: normal;
  text-decoration: none;
}

nav a,
nav a:visited {
  color: white;
}

nav a:hover,
nav a:focus {
  color: #F94E6D;
}

a.active-link {
  color: #F94E6D !important;
}

.icon-wrap {
  border: none;
  outline: none;
  background: transparent;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin: auto 0;
  padding: 1rem .75rem;
  width: 3rem;
  height: 3.5rem;
  cursor: pointer;
}

.icon-wrap:hover {
  outline: none;
}

.icon {
  width: 1.5rem;
  height: .25rem;
  background: #fff;
}

#icon-center {
  margin: .375rem 0;
  transition: opacity .3s .2s;
}

#icon-top {
  position: relative;
  top: 0;
  transition: transform .3s, top .3s .3s, background .3s .3s;
}

#icon-bottom {
  position: relative;
  bottom: 0;
  transition: transform .3s, bottom .3s .3s, background .3s .3s;
}

.menuActive .icon {
  background: #F94E6D;
}

.menuActive #icon-center {
  opacity: 0;
  transition: opacity .1s .3s;
}

.menuActive #icon-top {
  top: .625rem;
  transform: rotateZ(45deg) scaleX(1.2);
  /* moves line down to center before rotating and scaling */
  transition: top .3s, transform .3s .3s;
}

.menuActive #icon-bottom {
  bottom: .625rem;
  transform: rotateZ(-45deg) scaleX(1.2);
  /* moves line up to center before rotating and scaling */
  transition: bottom .3s, transform .3s .3s;
}

nav.menuActive ul {
  visibility: visible;
  transform: translateY(0);
}

nav.menuActive ul li {
  opacity: 1;
  height: 3rem;
}

nav.menuActive ul li a {
  height: 3rem;
  display: block;
}

@media (min-width: 768px) {

  .icon-wrap {
    display: none;
  }

  nav {
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
  }

  #monstera {
    height: 1.75rem;
  }

  nav h3 {
    font-size: 1.75rem;
  }

  nav ul {
    visibility: visible;
    transform: translateY(0);
    transition: none;
    display: flex;
    width: auto;
  }

  nav ul li {
    border: none;
    margin-left: 1rem;
    height: initial;
    opacity: 1;
  }

  nav ul li a {
    height: initial;
    padding: .75rem;
  }

}